.tab,
.tabs{
  display: flex;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: .1875em;
    border-radius: 3.125em;
    background-color: #2e3035;
  }
}

.tab{
  padding: 0 .25em .75em;
  display: flex;
  align-items: center;
  position: relative;
  font-weight: 700;
  transition: all .3s;
  @include lastM(0 .94em 0 0);
  &:before{
    background-color: $primary;
    opacity: 0;
    transition: all .3s;
  }
  span{
    font-size: 1em;
    line-height: 125%;
  }
  &:hover{
    cursor: pointer;
  }
  &._active{
    color: $red-2;
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
