.main{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  &__bottom{
    margin-top: auto;
    padding: 1em .75em 0;
  }
}

.main-header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .5em .75em;
  margin-top: -1em;
  &__action,
  &__user{
    display: flex;
    align-items: center;
  }
  &__user{
    &-img{
      width: 2em;
      height: 2em;
      flex-shrink: 0;
      object-fit: cover;
      margin-right: .75em;
    }
  }
  &__wallet{
    margin-left: 1.25em;
    width: 1em;
    @include center;
    img{
      width: 100%;
    }
  }
  .lang{
    margin-left: 1.25em;
  }
}

.user-info{
  margin-top: .5em;
  .m-card__inner{
    padding: 1em .75em;
  }
  &__balance{
    text-transform: uppercase;
    font-family: $font2;
    min-width: 7.375em;
    img{
      width: 2em;
      margin-right: .75em;
    }
  }
  &__upgrade{
    margin-left: 1.25em;
    padding: 0 1em;
    display: flex;
    justify-content: center;
    flex-direction: column;
    min-height: 2em;
    border-left: 1px solid $white;
  }
  &__num{
    font-size: 1em;
    font-family: $font2;
  }
  &__next{
    font-size: .625em;
    line-height: 120%;
    letter-spacing: 0.02em;
    margin-top: .4em;
  }
  &__btn{
    width: 2em;
    @include center;
    margin-left: auto;
    position: relative;
    img{
      width: 100%;
      transition: all .3s;
    }
    &:before,
    ._view2{
      opacity: 0;
      @include centerAbsolute;
    }
    &:before{
      content: '';
      transition: all .3s;
      width: 2em;
      height: 2em;
      background-image: url(../img/arrow-top3.svg);
      @include bgImgSize(100%);
      filter: blur(4px);
    }
    &._light{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
      &:before{
        opacity: 1;
      }
    }
  }
}

.main-slider{
  margin-top: 2em;
  &__top{
    text-align: center;
    font-size: 1.5em;
    font-family: $font2;
    margin-bottom: 1em;
    text-transform: uppercase;
  }
  .swiper{
    &-pagination{
      position: static;
      display: inline;
      color: $gray;
    }
    &-container{
      width: calc(100% + 1.5em);
      margin-left: -.75em;
      overflow: hidden;
      position: relative;
    }
    &-button{
      width: 2em;
      height: 2em;
      @include center;
      transform: translateY(-50%);
      margin: 0;
      img{
        width: .5em;
      }
      &-prev{
        left: 1.25em;
      }
      &-next{
        right: 1.25em;
      }
    }
  }
  &__img{
    height: 15em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}
