.boost-quest{
  margin-top: 1em;
  .boost-card2{
    margin-top: .75em;
  }
}

.boost-card2{
  .m-card__inner{
    padding: 1em;
  }
  &__top{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__items{
    @include grid100gap(.75em);
  }
  &__item,
  &__item2{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: .875em;
    height: .875em;
    @include center;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__item{
    font-weight: 700;
    .boost-card2__icon{
      padding: .0625em;
    }
  }
  &__item2{
    font-weight: 500;
    span{
      font-size: .875em;
    }
  }
  &__btn{
    min-width: 4em;
  }
  &__bottom{
    margin-top: 1em;
    padding-top: .5em;
    border-top: 1px solid #22242b;
  }
  &__desc{
    font-size: .75em;
    line-height: 120%;
    color: $gray;
  }
}

.boost-card3{
  .m-card__inner{
    padding: .75em .875em;
  }
  &__title{
    font-size: .75em;
    line-height: 120%;
    font-weight: 700;
  }
  &__wrap{
    display: flex;
    margin-top: .3125em;
    margin-left: -1em;
    width: calc(100% + .875em);
  }
  &__icon{
    width: 5em;
    flex-shrink: 0;
    margin-right: .25em;
    position: relative;
    img{
      width: 100%;
      display: block;
      position: relative;
      z-index: 2;
    }
  }
  &__shadow{
    position: absolute;
    width: 1.5625em;
    height: 1.5625em;
    border-radius: 50%;
    filter: blur(20px);
    top: .5em;
    left: 1.1875em;
  }
  &__info{
    padding-top: .375em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__price{
    display: flex;
    align-items: center;
    font-weight: 500;
    img{
      width: .75em;
      margin-right: .375em;
    }
  }
  &__btn{
    margin-top: .5em;
  }
}
