.preloader{
  &__logo{
    width: 17.25em;
    margin: 0 auto;
    max-width: 98%;
    display: block;
  }
  &__img{
    width: 100%;
    display: block;
    margin-top: 1em;
  }
  &__bottom{
    margin-top: 2em;
    padding: 0 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__text{
    letter-spacing: 0.02em;
    text-align: center;
  }
  &__wrap{
    min-width: 15.5em;
    border-top: 1px solid #38393d;
    margin-top: 1em;
    padding-top: 1.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo2{
    width: 3.875em;
  }
  &__link{
    @include center;
    img{
      height: .75em;
      display: block;
    }
  }
  &__content{
    position: relative;
  }
  .loader{
    left: 50%;
    bottom: .75em;
    position: absolute;
    transform: translateX(-50%);
    z-index: 2;
    width: 15.5em;
    max-width: 98%;
  }
}

.loader{
  &__num{
    font-weight: 500;
    font-size: .625em;
    line-height: 120%;
    letter-spacing: 0.02em;
    color: $primary;
  }
  &__progress{
    margin-top: .75em;
    background-color: #474747;
    height: .25em;
    border-radius: 1.25em;
    overflow: hidden;
    position: relative;
    span{
      background-color: $primary;
      height: 100%;
      display: block;
      max-width: 100%;
    }
  }
}
