.boost{
  .tabs{
    margin: 2.5em 0 1.5em;
  }
}

.boost-balance{
  &__title{
    text-align: center;
    letter-spacing: 0.02em;
  }
  &__wrap{
    margin-top: .75em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.125em));
    gap: 2.25em;
    align-items: center;
    position: relative;
    &:before{
      content: '';
      @include centerAbsolute;
      width: 3px;
      height: 1.625em;
      background-color: $primary;
    }
  }
  &__item{
    display: flex;
    align-items: center;
    font-family: $font2;
    text-transform: uppercase;
    &:nth-child(1){
      justify-content: flex-end;
    }
    span{
      font-size: 1.5em;
    }
    img{
      width: 2em;
      margin-right: .75em;
    }
  }
}

.m-subtitle{
  color: #6a6d74;
  font-weight: 500;
  font-size: 1em;
  line-height: 125%;
}

.boost-upgrades{
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
    margin-top: 1em;
  }
}

.m-card{
  padding: 1px;
  background: linear-gradient(180deg, rgba(128, 131, 163, 0.6) 0%, rgba(48, 49, 61, 0.2) 100%);
  border-radius: .75em;
  position: relative;
  &__inner{
    background: #161922;
    padding: .6875em;
    border-radius: inherit;
    min-height: 100%;
  }
}

.boost-card{
  &._shadow{
    .boost-card__icon{
      position: relative;
      &:before{
        content: '';
        position: absolute;
        width: 1.5625em;
        height: 1.5625em;
        background-color: #c00;
        border-radius: 50%;
        filter: blur(20px);
        top: .25em;
        left: .25em;
      }
      img{
        //position: relative;
        //z-index: 2;
      }
    }
  }
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: 1em;
  }
  &__icon{
    @include center;
    width: 2.375em;
    flex-shrink: 0;
    margin-right: .5em;
    img{
      width: 100%;
    }
  }
  &__title{
    font-size: 1em;
    font-weight: 700;
  }
  &__desc{
    font-size: .625em;
    line-height: 120%;
    color: $gray;
  }
  &__num{
    position: absolute;
    top: -.5em;
    right: -.25em;
    min-width: 2.25em;
    height: 1.25em;
    padding: 0 .5em;
    border-radius: .75em;
    background-color: $primary;
    font-weight: 700;
    text-align: center;
    @include center;
    span{
      letter-spacing: 0.02em;
      font-size: .625em;
    }
  }
}


