.menu{
  border-top: 2px solid #3f3f3f;
  background: #161922;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 10;
  width: 100%;
  padding: 0 .8125em 1em;
  display: flex;
  justify-content: space-between;
  &__btn{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: .625em 0;
    color: #757575;
    flex-grow: 1;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      height: 2px;
      width: 3em;
      bottom: 100%;
      left: 50%;
      transform: translateX(-50%);
      opacity: 0;
      background-color: $primary;
      transition: all .3s;
    }
    &._active{
      color: $white;
      &:before{
        opacity: 1;
      }
    }
    img{
      height: 1.375em;
      margin-bottom: .25em;
    }
    span{
      font-size: .625em;
      line-height: 120%;
      letter-spacing: 0.05em;
    }
  }
}


