.game{
  flex-grow: 1;
  padding-bottom: 2em;
}

.game-top{
  margin-top: -.25em;
  color: #fff;
  .m-card__inner{
    padding: .875em 1.25em;
    display: flex;
    align-items: center;
    min-height: 3.875em;
    position: relative;
  }
  &__wrap{
    width: 100%;
  }
  &__sum{
    font-weight: 500;
    img{
      width: 1.25em;
      margin-right: .5em;
    }
  }
  &__time{
    @include centerAbsolute;
    padding: 0 .625em;
    border: 1.5px solid $primary;
    height: 2.25em;
    display: flex;
    align-items: center;
    font-weight: 500;
    border-radius: .625em;
  }
}

.game-area{
  margin-top: 1.5em;
  table{
    width: 100%;
    border-spacing: 0;
  }
  td{
    border: 1px solid #22242b;
  }
  tr{
    &:first-child{
      td{
        &:first-child{
          border-top-left-radius: .75em;
        }
        &:last-child{
          border-top-right-radius: .75em;
        }
      }
    }
    &:last-child{
      td{
        &:first-child{
          border-bottom-left-radius: .75em;
        }
        &:last-child{
          border-bottom-right-radius: .75em;
        }
      }
    }
  }
  &__td{
    padding-bottom: 100%;
    position: relative;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.game-nav{
  position: fixed;
  left: 0;
  bottom: 0;
  padding: 0 .75em 3.25em;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(6,calc(100% / 6 - 2.5em / 6));
  gap: .5em;
  &__btn{
    height: 3.25em;
    position: relative;
    .m-card__inner{
      padding: 0;
    }
  }
  &__shadow{
    filter: blur(20px);
    position: absolute;
    top: .375em;
    left: .4375em;
    width: 2.25em;
    height: 2.25em;
    border-radius: 50%;
  }
  &__icon{
    z-index: 2;
    @include centerAbsolute;
    max-width: 2.625em;
    max-height: 2.625em;
  }
  &__num{
    position: absolute;
    min-width: 1.75em;
    border-radius: .375em;
    right: -.25em;
    bottom: .25em;
    z-index: 3;
    .m-card__inner{
      padding: 0 .25em;
      @include center;
      min-height: 1.125em;
    }
  }
}
