.friends{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding-top: .5em;
  &__top{
    text-align: center;
    @include grid100gap(1em);
  }
  &__title{
    font-size: 1.5em;
    text-transform: uppercase;
    font-weight: 700;
  }
  &__desc{
    font-size: .75em;
    line-height: 120%;
    letter-spacing: 0.05em;
    color: $gray;
  }
  &__bottom{
    margin-top: auto;
    padding: 1em .75em 0;
  }
  &__nav{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
  }
  &__content{
    margin-top: 1.5em;
  }
  &__items{
    margin-top: .75em;
    @include grid100gap(.5em);
  }
}

.friend-btn,
.friend-card{
  &__price{
    display: flex;
    align-items: center;
    font-weight: 700;
    text-transform: uppercase;
    img{
      width: 1.25em;
      margin-right: .5em;
    }
  }
}

.friend-btn{
  .m-card__inner{
    padding: 1em;
    display: flex;
    flex-direction: column;
  }
  &__text{
    font-size: .625em;
    line-height: 120%;
    letter-spacing: 0.05em;
    margin-bottom: .67em;
  }
  &__price{
    margin-top: auto;
    color: $primary;
  }
}

.friend-card{
  .m-card__inner{
    padding: .625em 1em .625em .625em;
    display: flex;
    align-items: center;
  }
  &__img{
    width: 2.25em;
    height: 2.25em;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  &__info{
    @include grid100gap(.25em);
    font-weight: 500;
  }
  &__title{
    font-size: .875em;
    line-height: 120%;
  }
  &__desc{
    font-size: .625em;
    line-height: 120%;
    color: $gray;
  }
  &__price{
    margin-left: auto;
    text-transform: uppercase;
    span{
      font-size: .75em;
    }
  }
}

.friend-modal{
  .m-card__inner{
    padding-top: 2em;
    padding-bottom: 2.875em;
  }
  &__info{
    padding: 0 .75em;
  }
  &__title{
    margin-bottom: .75em;
  }
  &__nav{
    margin-top: 1.5em;
    @include grid100gap(1em);
  }
  &__btn{
    width: 100%;
    color: $white;
  }
}
