@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 .5em;
    height: 1.875em;
    font-weight: 700;
    border-radius: .5em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .875em;
        letter-spacing: .02em;
    }
}

.m-btn-big{
    height: 3em;
    border-radius: .625em;
}

.m-btn-red{
    background-color: $red-2;
    color: $white;
    &:hover{
        background-color: darken($red-2,10%);
        color: $white;
    }
    &:disabled{
        opacity: .5;
        pointer-events: none;
    }
}

.m-btn-white{
    background-color: $white;
    color: #161922;
    &:hover{
        background-color: darken($white,10%);
        color: #161922;
    }
}

.m-btn-white-outline{
    border: 1px solid $white;
    color: #D7D7D7;
    &:hover{
        background-color: $white;
        color: #161922;
    }
}
