.tasks{
  &__top{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: relative;
    .hint{
      position: absolute;
      top: 0;
      right: 0;
    }
  }
  &__coin{
    width: 5.75em;
    height: 5.75em;
    @include center;
    margin-bottom: .25em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 120%;
    letter-spacing: 0.02em;
    font-weight: 700;
    padding: 0 2em;
  }
  &__content{
    margin-top: 2.625em;
  }
}

.hint{
  &__btn{
    @include center;
    width: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.tasks-group{
  margin-top: 1.5em;
  &:first-child{
    margin-top: 0;
  }
  &__items{
    margin-top: .75em;
    @include grid100gap(.5em);
  }
}

.task-card{
  .m-card__inner{
    padding: 1em;
  }
  &__wrap{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__items{
    @include grid100gap(.625em);
  }
  &__item{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 1em;
    height: 1em;
    @include center;
    flex-shrink: 0;
    margin-right: .5em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__btn{
    min-width: 4em;
  }
  &__btn2{
    border: $border;
    img{
      width: .8125em;
    }
  }
}

.progress-circle{
  @include center;
  position: relative;
  svg{
    width: 1em;
  }
}
