.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .8;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        width: 67em;
        max-width: calc(100% - 3em);
        position: relative;
        z-index: 3;
        display: inline-block;
    }
    .m-card{
        border-radius: 1em;
        &__inner{
            padding: 2em 1.5em;
        }
    }
    &__title{
        letter-spacing: 0.02em;
        font-size: 1.5em;
        line-height: 120%;
        font-weight: 700;
        text-align: center;
    }
}

.finish-modal{
    &__img{
        width: 100%;
        display: block;
        mix-blend-mode: lighten;
        margin-top: -.875em;
    }
    &__desc{
        text-align: center;
        margin: -3.375em 0 4em;
    }
    .props{
        margin-top: -1.75em;
    }
    &__nav{
        margin-top: 1.5em;
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .5em));
        gap: 1em;
    }
    &__btn{
        width: 100%;
        height: 3em;
        img{
            width: .9375em;
            margin-right: .6875em;
        }
    }
}

.props{
    @include grid100gap(.5em);
}

.prop-item{
    display: flex;
    justify-content: space-between;
    &:after{
        content: '';
        height: 1px;
        flex-grow: 1;
        min-width: 2em;
        background-image: url(../img/line.svg);
        background-size: auto 100%;
        margin: .5625em .5em 0;
    }
    &__desc,
    &__title{
        font-size: 1em;
        line-height: 125%;
        font-weight: 700;
        letter-spacing: 0.02em;
        display: flex;
        align-items: center;
        img{
            width: 1.25em;
            margin-right: .5em;
        }
    }
    &__desc{
        order: 2;
        text-align: right;
    }
}

.modal2{
    .modal__dialog{
        position: fixed;
        left: 0;
        bottom: 0;
        max-height: $vh;
        overflow-y: auto;
        width: 100%;
        max-width: 100%;
    }
    .m-card{
        border-radius: 1em 1em 0 0;
        &__inner{
            padding: 3em .75em 1.25em;
        }
    }
}

.charge-modal{
    &__img{
        width: 2.75em;
        margin: 0 auto 1.5em;
        position: relative;
        &:before{
            content: '';
            position: absolute;
            left: 50%;
            bottom: .3125em;
            transform: translateX(-50%);
            width: 6em;
            height: 1.5em;
            border-radius: 100%;
            filter: blur(20px);
            background-color: $primary;
        }
        img{
            width: 100%;
            display: block;
            position: relative;
            z-index: 2;
        }
    }
    &__desc{
        margin-top: 1em;
        text-align: center;
    }
    &__price{
        @include center;
        font-weight: 500;
        margin-top: 1em;
        img{
            width: 1.75em;
            margin-right: .5em;
        }
    }
    &__btn{
        margin-top: 1.75em;
        width: 100%;
    }
}


