.main-action{
  padding-left: .75em;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  &__title{
    font-size: .875em;
    line-height: 120%;
    letter-spacing: 0.02em;
  }
  &__num{
    font-size: 1.5em;
    font-family: $font2;
    margin-top: .25em;
    text-transform: uppercase;
  }
  &__num2{
    font-size: .75em;
    line-height: 120%;
    margin-top: .67em;
  }
  &__buttons{
    display: flex;
    padding-bottom: .625em;
  }
  &__btn{
    height: 2.1875em;
    border: 1px solid #fff;
    color: #d7d7d7;
  }
  &__play{
    min-width: 5.3125em;
    margin-left: 1em;
  }
  &__boost{
    padding: 0 1.5em 0 .5em;
    position: relative;
    img{
      position: absolute;
      width: 2.5em;
      top: -.75em;
      right: -.875em;
    }
  }
}

.main-btn{
  margin-top: .625em;
  width: 100%;
  height: 3em;
  font-weight: 500;
  border-radius: .625em;
  overflow: hidden;
  position: relative;
  &__fill{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    max-width: 100%;
    background-color: $red-2;
    transition: all .3s;
  }
  &__time{
    position: absolute;
    top: 50%;
    right: .75em;
    transform: translateY(-50%);
    font-weight: 700;
    span{
      font-size: .5em;
    }
  }
  &--progress{
    background: #863139;
    &:hover{
      background: #863139;
    }
    span{
      position: relative;
      z-index: 2;
    }
  }
}

.bonus-modal,
.friend-modal{
  &__title{
    text-transform: uppercase;
    margin-bottom: .875em;
    font-weight: 700;
  }
  &__desc{
    font-size: .75em;
    line-height: 120%;
    letter-spacing: 0.05em;
    color: $gray;
  }
}

.bonus-modal{
  .m-card__inner{
    padding-top: 2em;
  }
  &__top{
    padding: 0 .75em;
    display: flex;
    justify-content: space-between;
  }
  &__items{
    display: flex;
  }
  &__item{
    width: .9375em;
    height: .9375em;
    margin-left: .2em;
    flex-shrink: 0;
    @include bgImgSize(100%);
    background-image: url(../img/flash.svg);
    &--red{
      background-image: url(../img/flash2.svg);
    }
  }
  &__btn{
    width: 100%;
    margin-top: 2.75em;
  }
}

.bonus-slider{
  width: calc(100% + 1.5em);
  margin: 1.875em 0 -1.25em -.75em;
  padding-bottom: 1.25em;
  overflow: hidden;
  &__wrap{
    width: 9.5em;
    margin: 0 auto;
  }
  .swiper{
    &-container{
      width: calc(100% + .5em);
    }
    &-slide{
      padding-right: .5em;
      color: rgba(#464749,.2);
      .bonus-card__icon{
        opacity: .2;
      }
      &-active,
      &-active~.swiper-slide{
        color: $white;
        .bonus-card__icon{
          opacity: 1;
        }
      }
      &-active{
        .bonus-card{
          background: $primary;
        }
      }
    }
  }
}

.bonus-card{
  transition: all .3s;
  border-radius: .75em !important;
  box-shadow: 0 0 1em 0 rgba(0, 0, 0, 0.4);
  .m-card{
    text-align: center;
    &__inner{
      padding: 1.25em 1em 1.5em;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
  &__icon{
    height: 2em;
    margin-bottom: .75em;
    transition: all .3s;
  }
  &__num{
    font-size: 1.5em;
    font-family: $font2;
  }
}
